/* General Body Styles */
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f7f6; /* Light gray background */
  color: #333;
}

/* App Container */
.task-app-container {
  max-width: 1000px;
  margin: 30px auto;
  padding: 25px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
}

/* Header */
.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding-bottom: 15px;
  border-bottom: 1px solid #eee;
}

.app-header h1 {
  color: #2c3e50; /* Dark blue-gray for headings */
  margin: 0;
  font-size: 2.2em;
  font-weight: 600;
}

/* Search Input */
.search-input {
  padding: 10px 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1em;
  width: 250px;
  transition: border-color 0.3s ease;
}

.search-input:focus {
  border-color: #3498db; /* Blue accent on focus */
  outline: none;
}

/* Form Styles */
.task-form {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
  padding: 20px;
  background-color: #fdfdfd;
  border: 1px solid #eee;
  border-radius: 8px;
}

.task-form input[type="text"],
.task-form input[type="date"],
.task-form select,
.task-form button {
  padding: 12px 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1em;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.task-form input:focus,
.task-form select:focus {
  border-color: #3498db;
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.2);
  outline: none;
}

.task-form button {
  background-color: #3498db; /* Blue button */
  color: white;
  border: none;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.3s ease;
}

.task-form button:hover {
  background-color: #2980b9; /* Darker blue on hover */
}

/* Table Styles */
.task-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  background-color: #ffffff;
  border-radius: 8px;
  overflow: hidden; /* Ensures rounded corners apply to children */
}

.task-table th,
.task-table td {
  padding: 15px;
  text-align: left;
  border-bottom: 1px solid #eee;
}

.task-table th {
  background-color: #ecf0f1; /* Light gray for table headers */
  color: #555;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 0.9em;
}

.task-table tbody tr:hover {
  background-color: #f8f8f8;
}

/* Assignee specific styles */
.assignee-item {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  padding: 8px;
  border: 1px solid #f0f0f0;
  border-radius: 5px;
  background-color: #fafafa;
}

.assignee-item label {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  font-weight: 500;
}

.assignee-item input[type="checkbox"] {
  margin-right: 8px;
  transform: scale(1.2);
}

.assignee-item .delete-button {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.delete-button:hover {
  background-color: #da190b;
}


.assignee-item textarea:focus {
  border-color: #3498db;
  box-shadow: 0 0 0 2px rgba(52, 152, 219, 0.15);
  outline: none;
}

/* Assignee Input Section */
.assignee-input-section {
  display: flex;
  gap: 10px;
  align-items: center;
  grid-column: 1 / -1; /* Span full width */
}

.assignee-input {
  flex-grow: 1;
}

.add-assignee-button {
  background-color: #27ae60;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.add-assignee-button:hover {
  background-color: #229954;
}

/* Selected Assignees */
.selected-assignees {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 10px;
  padding: 10px;
  background-color: #f9f9f9;
  border-radius: 5px;
  grid-column: 1 / -1; /* Span full width */
}

.selected-assignee {
  display: flex;
  align-items: center;
  background-color: #e0e0e0;
  padding: 5px 10px;
  border-radius: 15px;
  font-size: 0.9em;
}

.remove-assignee-button {
  background: #c0392b;
  color: white;
  border: none;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  margin-left: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  line-height: 1;
  transition: background-color 0.3s;
}

.remove-assignee-button:hover {
  background: #a93226;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.2);
  width: 90%;
  max-width: 500px;
  z-index: 1001;
}

.modal-content h2 {
  margin-top: 0;
  color: #2c3e50;
}

.add-assignee-form {
  display: flex;
  margin-bottom: 20px;
}

.add-assignee-form input {
  flex-grow: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px 0 0 4px;
}

.add-assignee-form button {
  padding: 10px 15px;
  border: none;
  background-color: #3498db;
  color: white;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
}

.assignee-management-list {
  list-style: none;
  padding: 0;
  max-height: 300px;
  overflow-y: auto;
}

.assignee-management-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #eee;
}

.delete-assignee-button {
  background: #e74c3c;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
}

.close-modal-button {
  display: block;
  margin: 20px auto 0;
  padding: 10px 20px;
  border: 1px solid #ccc;
  background: #f0f0f0;
  border-radius: 5px;
  cursor: pointer;
}

.manage-assignees-button {
    background-color: #95a5a6;
    color: white;
    margin-right: 15px;
}

.manage-assignees-button:hover {
    background-color: #7f8c8d;
}
